@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loginContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    font-family: 'Alata', sans-serif;
}

.loginRight{
    display: flex;
    width: 50vw;
    height: 100vh;
    background-color: blue;
}

.loginLeft{
    width: 50vw;
    height: 100vh;
    background-color: #4B4374 ;
}

.loginContent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:10vh 12vw 10vh 12vw;
    height: 80vh;
    background-color: #332C62  ;
    border-radius: 5%;
    
}


.botonIngresar {
    margin: 20px;
	background-color:#6797c4;
	border-radius:4px;
    border: none;
    width: 204px;
    height: 40px;
    font-size: medium;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    cursor: pointer;
    color: #fff ;
}

.botonIngresar:hover{
    background-color:#4478a8;
}

*:focus {
    outline: 0 !important;
}

.labelError{
    color: #B23460;
}

.label-alert{
    color: #B23460;
}

.BackgroundImage{
    display: flex;
    width: 50vw;
    align-items: center;
    justify-content: center;

}
.titleContainer{
    display: flex;
    color: white;
    font-size: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: blod;
}
.labelContent{
    color: #7FB0EA;
}
.errorStyle{
        border: none;
        color: #D35584;
        background-color: #BA3D67;
        border-radius: 4px;
        font-size: 14px;
        width: 200px;
        height: 40px;
        font-weight: bold;
}

.regularStyle{
        margin: 5px;
        border: none;
        color: #332C62;
        background-color: #B7ADC5;
        border-radius: 4px;
        font-size: 14px;
        text-align: center;
        width: 200px;
        height: 40px;
        font-weight: bold;
}

.regularStyle::-webkit-input-placeholder{
        color: #332C62;
        opacity: .8;
        font-size: 14px;
        font-weight: bold;
}

.regularStyle:-ms-input-placeholder{
        color: #332C62;
        opacity: .8;
        font-size: 14px;
        font-weight: bold;
}

.regularStyle::-ms-input-placeholder{
        color: #332C62;
        opacity: .8;
        font-size: 14px;
        font-weight: bold;
}

.regularStyle::placeholder{
        color: #332C62;
        opacity: .8;
        font-size: 14px;
        font-weight: bold;
}
